@import url(./reset.less);
@import url(./header.less);
@import url(./footer.less);
@import url(./video-box.less);
@import url(./fixed-bar.less);
.fl {
  float: left;
}
.fr {
  float: right;
}
.wrapper {
  width: 100%;
  min-width: 1506px;
}
.g-wrap {
  margin: 0 auto;
  position: relative;
  height: 100%;
}
.video-player {
  z-index: 99999;
  position: fixed;
  top: 0;
  width: 100%;
  bottom: 0;
  background: #000;
  .video-tag {
    width: 100%;
    height: 100%;
    display: block;
  }
  .close {
    width: 30px;
    height: 30px;
    background: url('../images/close-video.png') no-repeat center;
    background-size: 30px;
    position: absolute;
    right: 30px;
    top: 30px;
  }
}
.helper {
  position: fixed;
  top: 162px;
  right: 0;
  width: 96px;
  height: 194px;
  .helper-item {
    background-color: #7e318e;
    margin-bottom: 2px;
    height: 96px;
    width: 96px;
    img {
      margin-top: 16px;
      margin-left: 23px;
      width: 50px;
      height: 50px;
    }
    p {
      color: #fff;
      margin-top: 5px;
      margin-left: 21px;
    }
  }
}
.adult {
  position: relative;
  .form-box {
    height: 354px;
    width: 347px;
    background-color: #fcfcfc;
    position: absolute;
    top: 45%;
    right: 5%;
    &.index {
      top: 65%;
      right: 3%;
    }
    .form-content {
      width: 218px;
      height: 300px;
      margin: 27px auto;
      position: relative;
      .capcha {
        margin-top: 5px;
      }
      .form-pic {
        position: absolute;
        right: 1px;
        border: 1px solid #e5e5e8;
        border-left: none;
        width: 32px;
        height: 30px;
        padding: 5px 1px 5px 9px;
        &.pic-age {
          top: 91px;
        }
        img {
          width: 15px;
          height: 15px;
        }
      }
      #adult-mobile {
        margin-top: 43px;
      }
      #get-capcha, #reset-capcha {
        font-size: 13px;
        float: right;
        width: 93px;
        height: 30px;
        color: #fff;
        background-color: #7e318e;
        border-radius: 3px;
      }
      #submit {
        font-size: 20px;
        margin-top: 5px;
        color: #fff;
        width: 100%;
        height: 45px;
        background-color: #7e318e;
      }
      input {
        margin-bottom: 10px;
        padding-left: 5px;
        width: 100%;
        height: 30px;
        border: 1px solid #e5e5e8;
        &.with-icon {
          width: 186px;
        }
        &.with-capcha {
          width: 117px;
        }
      }
      p {
        color: #727171;
        text-align: center;
        font-size: 24px;
        margin-bottom: 15px;
      }
    }
  }
}