.g-ft {
  background-color: #666464;
  overflow: hidden;
  min-width: 1200px;
  .footer-content {
    margin: 0 auto;
    padding-bottom: 105px;
    width: 1506px;
    height: 100%;
    overflow: hidden;
    border-bottom: solid 1px #c8caca;
    .tel {
      width: 230px;
      height: 40px;
      background-image: url('../images/tel.png');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 100% 100%
    }
    .index-button {
      margin-top: 20px;
      text-align: center;
      height: 50px;
      line-height: 50px;
      background-color: #7e318e;
      border-radius: 3px;
      font-size: 20px;
      font-weight: bold;
    }
    .fl, .fr {
      margin-top: 60px;
      li {
        float: left;
        color: #fff;
        margin-right: 100px;
        h4 {
          margin-bottom: 45px;
          font-weight: bold;
          font-size: 21px;
        }
        a {
          margin-bottom: 10px;
          display: block;
          color: #c8caca;
          font-size: 18px;
        }
        p {
          color: #c8caca;
          margin-bottom: 10px;
          font-size: 18px;
        }
      }
    }
    &.bottom {
      li {
        margin-top: 25px;
        a {
          padding-right: 45px;
          font-size: 18px;
          color: #c8caca;
        }
      }
      h4 {
        margin-top: 40px;
        padding-bottom: 12px;
        font-weight: bold;
        font-size: 21px;
        color: #fff;
        display: inline-block;
        border-bottom: solid 2px #7e318e;
      }
    }
  }
}

